"use client";

import { motion } from "framer-motion";
import { ArrowRight, Sparkles } from "lucide-react";
import { useEffect, useState } from "react";
import FloatingParticles from "@/components/landing/FloatingParticles";

const promptText =
  "Build a marketplace for organic coffee beans with subscriptions.";

function TypingDemo() {
  const [displayText, setDisplayText] = useState("");
  const [phase, setPhase] = useState<"typing" | "generating">("typing");

  useEffect(() => {
    let timeout: NodeJS.Timeout;
    let interval: NodeJS.Timeout;

    if (phase === "typing") {
      let index = 0;
      interval = setInterval(() => {
        index += 1;
        setDisplayText(promptText.slice(0, index));
        if (index >= promptText.length) {
          clearInterval(interval);
          timeout = setTimeout(() => setPhase("generating"), 600);
        }
      }, 45);
    }

    if (phase === "generating") {
      timeout = setTimeout(() => {
        setDisplayText("");
        setPhase("typing");
      }, 2400);
    }

    return () => {
      clearInterval(interval);
      clearTimeout(timeout);
    };
  }, [phase]);

  return (
    <div className="min-h-[120px] space-y-4">
      <div className="flex items-start gap-3">
        <div className="mt-1 h-2.5 w-2.5 rounded-full bg-emerald-400 shadow-[0_0_12px_rgba(52,211,153,0.6)]" />
        <p className="font-mono text-sm text-slate-200 sm:text-base">
          <span className="text-slate-400">$</span> {displayText}
          <span className="ml-1 inline-block h-4 w-2 translate-y-[2px] bg-indigo-300/70 animate-blink" />
        </p>
      </div>
      {phase === "generating" && (
        <div className="flex items-center gap-2 text-sm text-indigo-200">
          <Sparkles className="h-4 w-4" />
          <span>Generating</span>
          <span className="flex gap-1">
            <span className="h-1.5 w-1.5 animate-bounce rounded-full bg-indigo-300" />
            <span className="h-1.5 w-1.5 animate-bounce rounded-full bg-indigo-300 [animation-delay:120ms]" />
            <span className="h-1.5 w-1.5 animate-bounce rounded-full bg-indigo-300 [animation-delay:240ms]" />
          </span>
        </div>
      )}
    </div>
  );
}

export default function Hero() {
  return (
    <section className="relative overflow-hidden pt-32 pb-24">
      <div className="absolute inset-0 mesh-bg opacity-80" />
      <div className="absolute -top-32 left-1/2 h-[420px] w-[420px] -translate-x-1/2 rounded-full bg-indigo-500/20 blur-[120px]" />
      <FloatingParticles />
      <div className="relative z-10 mx-auto flex w-full max-w-6xl flex-col items-center px-6">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          className="text-center"
        >
          <p className="mb-4 inline-flex items-center gap-2 rounded-full border border-white/10 bg-white/5 px-4 py-1 text-xs uppercase tracking-[0.3em] text-indigo-200">
            <span className="h-2 w-2 rounded-full bg-emerald-400" />
            Autonomous Web Developer
          </p>
          <h1 className="text-balance text-4xl font-semibold text-white sm:text-5xl lg:text-6xl">
            <span className="bg-gradient-to-r from-indigo-400 via-purple-400 to-violet-300 bg-clip-text text-transparent text-glow">
              Describe it. See it. Deploy it. Instantly.
            </span>
          </h1>
          <p className="mx-auto mt-6 max-w-2xl text-lg text-slate-200">
            The world&apos;s first autonomous AI web developer. Zero code, zero
            human intervention.
          </p>
          <div className="mt-8 flex flex-col items-center justify-center gap-4 sm:flex-row">
            <button className="group inline-flex items-center gap-2 rounded-full bg-gradient-to-r from-indigo-500 to-purple-600 px-6 py-3 text-sm font-semibold shadow-[0_0_30px_rgba(99,102,241,0.45)] transition hover:scale-105">
              Start Building Free
              <ArrowRight className="h-4 w-4 transition group-hover:translate-x-1" />
            </button>
            <button className="rounded-full border border-white/15 px-6 py-3 text-sm font-semibold text-slate-100 transition hover:border-white/30 hover:text-white">
              Watch a Demo
            </button>
          </div>
        </motion.div>

        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.9, delay: 0.2 }}
          className="mt-12 w-full"
        >
          <div className="mx-auto w-full max-w-3xl rounded-2xl border border-white/10 bg-white/5 p-6 backdrop-blur-xl panel-glow">
            <div className="flex items-center gap-2 text-xs text-slate-400">
              <span className="h-2.5 w-2.5 rounded-full bg-red-500/80" />
              <span className="h-2.5 w-2.5 rounded-full bg-amber-400/80" />
              <span className="h-2.5 w-2.5 rounded-full bg-emerald-400/80" />
              <span className="ml-2">webgen-ai / prompt</span>
            </div>
            <div className="mt-6 rounded-xl border border-white/10 bg-slate-950/70 p-5">
              <TypingDemo />
            </div>
            <div className="mt-6 grid gap-4 text-xs text-slate-400 sm:grid-cols-3">
              <div>
                <p className="text-slate-200">Deploy Target</p>
                <p>Vercel + Supabase</p>
              </div>
              <div>
                <p className="text-slate-200">Build Time</p>
                <p>28 seconds</p>
              </div>
              <div>
                <p className="text-slate-200">Status</p>
                <p className="text-emerald-300">Live + Running</p>
              </div>
            </div>
          </div>
        </motion.div>
      </div>
    </section>
  );
}
